.page {
  background-color: #EFEFEF;
}
.tabbar-wrap {
  height: 3.125rem;
  position: fixed;
  bottom: 0;
  /* 距离底部为0像素 */
  left: 0;
  /* 左侧与视口边界对齐 */
  width: 100%;
  /* 宽度等于视口宽度 */
  z-index: 9999;
}
.home-page {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: transparent;
  color: #444;
}
