.title-wrap {
  width: 100%;
  padding-bottom: 0.625rem;
  padding-top: 0.625rem;
  background-color: #fff;
}
.title-wrap .title {
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: center;
  vertical-align: middle;
}
.title-wrap .title .left-icon {
  display: inline-block;
  background-image: url(https://img.limeetpet.com/limeet/title.png);
  overflow: hidden;
  width: 1.75rem;
  height: 0.625rem;
  margin-right: 1.25rem;
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
  margin-top: 0.625rem;
}
.title-wrap .title .right-icon {
  display: inline-block;
  background-image: url(https://img.limeetpet.com/limeet/title.png);
  width: 1.75rem;
  height: 0.625rem;
  margin-left: 1.25rem;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
  margin-top: 0.625rem;
}
.title-wrap .title h3 {
  display: inline-block;
  border-radius: 0.78125rem;
  background-color: #F6912D;
  color: #fff;
  text-align: center;
  font-weight: 400;
  padding-left: 1.875rem;
  padding-right: 1.875rem;
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
}
